<template>
    <div class="city-task-container">
        <div class="table-box">
            <div class="my-table-header">
                <span style="width: 200px">排名</span>
                <span style="width: 200px">区</span>
                <span style="width: 200px">PM<span style="font-size:12px;">2.5</span>浓度</span>
                <span style="width: 200px">环比</span>
                <span style="width: 200px">同比</span>
            </div>
            <div class="my-table-body">
                <a
                    href="javascript:;"
                    @click="goToDetails(item)"
                    v-for="(item, index) in tableData"
                    :key="index"
                    class="my-table-content"
                >
                        <div style="width: 200px">{{ item.pm25_rank }}</div>

                        <div style="width: 200px">{{ item.name }}</div>
                        <div style="width: 200px">
                            <span
                                class="bg-green bg-span"
                                v-if="item.pm25 <= 50"
                                >{{ item.pm25 }}</span
                            >
                            <span
                                class="bg-yellow bg-span"
                                v-else-if="
                                    item.pm25 > 50 && item.pm25 <= 100
                                "
                                >{{ item.pm25 }}</span
                            >
                            <span
                                class="bg-orange bg-span"
                                v-else-if="
                                    item.pm25 > 100 && item.pm25 <= 150
                                "
                                >{{ item.pm25 }}</span
                            >
                            <span class="bg-red bg-span" v-else-if="item.pm25 > 150&& item.pm25 <= 200">{{
                                item.pm25
                            }}</span>
                            <span class="bg-purple bg-span" v-else-if="item.pm25 > 200&& item.pm25 <= 300">{{
                                item.pm25
                            }}</span>
                            <span class="bg-brown bg-span" v-else-if="item.pm25 > 300">{{
                                item.pm25
                            }}</span>
                        </div>
                        <div style="width: 200px">
                            <div v-if="item.pm25_qoq <0">
                                <span class="text-red">{{
                                    Math.abs(item.pm25_qoq)
                                }}</span>
                                <span class="bg-icon-red"></span>
                            </div>
                            <div v-else-if="item.pm25_qoq >0">
                                <span class="text-green">{{
                                    item.pm25_qoq
                                }}</span>
                                <span class="bg-icon-green"></span>
                            </div>
                            <div v-else>
                                <span>{{
                                    item.pm25_qoq
                                }}</span>
                            </div>
                        </div>
                        <div style="width: 200px">
                            <div v-if="item.pm25_mom <0">
                                <span class="text-red">{{
                                    Math.abs(item.pm25_mom)
                                }}</span>
                                <span class="bg-icon-red"></span>
                            </div>
                            <div v-else>
                                <span class="text-green">{{
                                    item.pm25_mom
                                }}</span>
                                <span class="bg-icon-green"></span>
                            </div>
                        </div>

                </a>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:['tableData'],
    data() {
        return {
            // 表格数据
        };
    },
    created() {
    },
    methods: {
       
    },
};
</script>

<style lang="scss" scoped>
.city-task-container {
    
}
</style>