import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home'
import Layout from '@/views/Layout'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        component: Layout,
        redirect: '/home',
        children: [{
                path: '/home',
                name: 'Home',
                component: Home,
                
            },
            {
                path: '/industryOverview',
                name: 'IndustryOverview',
                component: () => import('@/views/IndustryOverview')
            },
            // Demeanour
            {
                path: '/demeanour',
                name: 'Demeanour',
                component: () => import('@/views/IndustryOverview/Demeanour')
            },
            {
                path: '/introduce',
                name: 'Introduce',
                component: () => import('@/views/IndustryOverview/Introduce')
            },
            {
                path: '/situation',
                name: 'Situation',
                component: () => import('@/views/IndustryOverview/Situation')
            },
            {
                path: '/briefing',
                name: 'Briefing',
                component: () => import('@/views/IndustryOverview/Briefing')
            },
            {
                path: '/policyPlanning',
                name: 'PolicyPlanning',
                component: () => import('@/views/PolicyPlanning')
            },
            {
                path: '/specificWork',
                name: 'SpecificWork',
                component: () => import('@/views/SpecificWork')
            },
            {
                path: '/dataAnalysis',
                name: 'DataAnalysis',
                component: () => import('@/views/DataAnalysis')
            },
            {
                path: '/ranking',
                name: 'Ranking',
                component: () => import('@/views/Ranking')
            },
            {
                path: '/assessment',
                name: 'Assessment',
                component: () => import('@/views/Assessment')
            },
            {
                path:'/home/earlyWarning',
                name:'EarlyWarning',
                component:()=> import('@/views/Home/EarlyWarning')
            },
            {
                path:'/home/hotFollow',
                name:'HotFollow',
                component:()=> import('@/views/Home/HotFollow')
            },
        ]
    },

]

const router = new VueRouter({
    // mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router