<template>
    <div class="air-ranking-container">
        <div class="public-box">
            <div class="header">
                <div class="public-title">大气排名</div>
                <div class="tab">
                    <a href="javascript:;" :class="activeTab == 'city' ? 'active' : ''" @click="tabClick('city')">市级</a>
                    <a href="javascript:;" :class="activeTab == 'county' ? 'active' : ''"
                            @click="tabClick('county')">区内</a>
                </div>
            </div>
            <!-- 表格 -->
            <CityRanking v-if="activeTab=='county'" :tableData="tableData" />
            <AreaRanking v-if="activeTab=='city'" :tableData="tableData"/>
        </div>
    </div>
</template>

<script>
import CityRanking from './CItyRanking'
import AreaRanking from './AreaRanking'
import {http} from '@/utils/request'

export default {
    components: {
          CityRanking,
          AreaRanking
    },
    data() {
        return {
            // 当前tab
            activeTab: "city",
            tableData:[]
        };
    },
    mounted() {
        let time = this.$moment().subtract(1, 'days').format('YYYY-MM-DD')
        this.getWorkDataList('2',time,this.activeTab)
    },
    methods: {
        tabClick(activeTab) {
            this.activeTab = activeTab;
            let time = this.$moment().subtract(1, 'days').format('YYYY-MM-DD')
            this.getWorkDataList('2',time,this.activeTab)
        },
        // 获取工作排名数据
        getWorkDataList(time_type,time,rank_type) {
            // time_type: 2: 日 4: 月 5: 年
            // time: 如果是月，是月的第1天。如果是年，是年的第1天
            // rank_type: city: 市 county: 区

            let json = {
                username: "api",
                 password: "a123456b",
                 time_type: time_type,
                 time: time,
                 rank_type: rank_type,
            }
            http.get("https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/ranks", { params:{
                json
            } }).then((res) => {
                if (res.data.code === 2000) {
                    this.tableData = res.data.result
                    if(this.activeTab == 'city'){
                        this.tableData = res.data.result.filter(item=>{
                            return item.name !=='北京市'
                        })
                    }
                    this.tableData = this.tableData.sort((a,b)=>{
                        return a.pm25_rank-b.pm25_rank
                    })
                } 
            });
        },
    },
};
</script>

<style lang="scss">
.air-ranking-container {
    .public-box {
        .header{
            display: flex;
            justify-content: space-between;
            .tab{
                margin-right:50px;
                a{
                    display: inline-block;
                    width:105px;
                    height:40px;
                    line-height:40px;
                    text-align: center;
                    color:#FFF6F6;
                    font-size: 24px;
                    background:#1D3B71;
                    &.active{
                        background:#5382D8;
                    }
                }
            }
        }   
    }
}
</style>