<template>
    <div class="da-xing-area-container">
        <div class="public-box">
            <div class="public-title">大兴区</div>
            <el-row :gutter="10">
                <el-col :span="8" style="margin-top:20px;">
                    <el-row v-if="PMobj">
                        <el-col :span="10">
                            <div class="pm-value">
                                {{PMobj.value}}
                            </div>
                        </el-col>    
                        <el-col :span="14">
                            <div>首要污染物：{{ PMobj.type }}</div>
                            <div>排名：{{ PMobj.rank }}</div>
                            <div>阴 --</div>
                            <div>优</div>
                        </el-col>    
                    </el-row>    
                </el-col>
                <el-col :span="8">
                    <span style="margin-left:50px;">大兴区</span>
                    <BarChartHorizontal
                        xTitleColor="#5470c6"
                        style="margin-top:-30px;"
                        height="200px"
                        BarChartHorizontalId="daXingAreaEchartsDataId"
                        v-if="daXingAreaEchartsData.length > 0"
                        :echartsData="daXingAreaEchartsData"
                        :yAxisTitle="daXingAreaEchartsDataYAxisTitle"
                    />
                </el-col>
                <el-col :span="8">
                    <span style="margin-left:50px;">北京市</span>
                    <BarChartHorizontal
                        style="margin-top:-30px;"
                        height="200px"
                        BarChartHorizontalId="bjCityEchartsDataId"
                        v-if="bjCityEchartsData.length > 0"
                        :echartsData="bjCityEchartsData"
                        :yAxisTitle="bjCityEchartsDataYAxisTitle"
                    />
                </el-col>
            </el-row>
            <el-row style="margin-top:-50px;">
                <div style="position:relative;left:80px; top:-20px;">PM<span style="font-size:12px;">2.5</span>浓度变化趋势</div>
                <!-- <div style="position:relative;left:80px; top:-20px;">浓度</div> -->
                <div class="tab">
                    <a href="javascript:;" :class="tabType == 'hour' ? 'active' : ''" @click="tabClick('hour')">最近24小时</a>
                    <a href="javascript:;" :class="tabType == 'day' ? 'active' : ''"
                            @click="tabClick('day')">最近30天</a>
                </div>
                <BarChart
                style="margin-top:-15px;"
                    height="250px"
                    :echartTitle="{
                        text:'单位：μg/m³',
                        left:'40px'
                    }"
                    v-if="ndChangeEchartsData.length > 0"
                    :echartsData="ndChangeEchartsData"
                    :xAxisTitle="ndChangeEchartsDataYAxisTitle"
                    :totalReportScoreData="totalReportScoreData"
                />
            </el-row>
        </div>
    </div>
</template>

<script>
import {http} from '@/utils/request'
import BarChartHorizontal from "@/components/Charts/BarChartHorizontal";
import BarChart from "@/components/Charts/BarChart";
export default {
    components: {
        BarChartHorizontal,
        BarChart
    },
    data() {
        return {
            PMobj:null,
            // 大兴区
            daXingAreaEchartsData: [],
            daXingAreaEchartsDataYAxisTitle: [],

            // 北京市
            bjCityEchartsData: [],
            bjCityEchartsDataYAxisTitle: [],

            // 浓度变化趋势
            ndChangeEchartsData: [],
            ndChangeEchartsDataYAxisTitle: [],
            totalReportScoreData:[],

            // tab按钮切换 默认24小时
            tabType:'hour'
        };
    },
    mounted() {
        // 大兴区 北京市 数据
        this.getAirsDataList();
        // 浓度变化 数据
        this.getNdChangeDataList();
    },
    methods: {
        // 大兴区 北京市 数据
        getAirsDataList() {
            let json = {username: "api", password: "a123456b"}
            http.get("https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/airs", { params:{
                json
            } }).then((res) => {
                if (res.data.code === 2000) {
                    // type value
                    this.daXingAreaEchartsData = res.data.result[1].vardata.map(item=>{
                        return item.value
                    })
                    this.daXingAreaEchartsDataYAxisTitle = res.data.result[1].vardata.map(item=>{
                        return item.type
                    })
                    this.daXingAreaEchartsDataYAxisTitle = this.daXingAreaEchartsDataYAxisTitle.filter(item=>{
                        return item != 'AQI' && item != '首要污染物'&& item != "污染等级"
                    })
                    this.bjCityEchartsData = res.data.result[0].vardata.map(item=>{
                        return item.value
                    })
                    this.bjCityEchartsDataYAxisTitle = res.data.result[0].vardata.map(item=>{
                        return item.type
                    })
                    this.bjCityEchartsDataYAxisTitle = this.bjCityEchartsDataYAxisTitle.filter(item=>{
                        return item != 'AQI' && item != '首要污染物'&& item != "污染等级"
                    })

                    this.PMobj = res.data.result[0].vardata[1]
                 
                } 
            });
        },
        // 浓度变化 数据
        getNdChangeDataList() {
            let json = {username: "api", password: "a123456b",type:this.tabType}
            http.get("https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/airperiod", { params:{
                json
            } }).then((res) => {
                if (res.data.code === 2000) {
                    this.ndChangeEchartsData = res.data.result.list[0].vardata[0].value
                    this.ndChangeEchartsDataYAxisTitle = res.data.result.ctime
                    if(this.tabType == 'hour'){
                        this.ndChangeEchartsDataYAxisTitle = this.ndChangeEchartsDataYAxisTitle.map(item=>{
                            return item = item.slice(11,19)
                        })
                    }else{
                        this.ndChangeEchartsDataYAxisTitle = this.ndChangeEchartsDataYAxisTitle.map(item=>{
                            return item = item.slice(0,11)
                        })
                    }
                    this.ndChangeEchartsDataYAxisTitle.forEach((item,index)=>{
                        if(index%4 !==1 ){
                            this.ndChangeEchartsDataYAxisTitle[index] = ''
                        }
                    })
                        

                    
                    this.totalReportScoreData = res.data.result.list[0].vardata[0].rank

                } 
            });
        },
        tabClick(activeTab) {
            this.tabType = activeTab;
            this.getNdChangeDataList();
        },
    },
};
</script>

<style lang="scss" scoped>
.da-xing-area-container{
    .pm-value{
        width:100%;
        text-align: center;
        height:100px;
        line-height: 100px;
        color:greenyellow;
        font-size: 50px;
    }
.tab{
               position: absolute;
               right:50px;
               top:20px;
               z-index: 5;
                a{
                    display: inline-block;
                    // width:105px;
                    padding:0 20px;
                    height:40px;
                    line-height:40px;
                    text-align: center;
                    color:#FFF6F6;
                    font-size: 16px;
                    background:#1D3B71;
                    &.active{
                        background:#5382D8;
                    }
                }
            }
}

</style>