<template>
    <div class="bar-chart-container">
        <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
        <div :id="BarChartHorizontalId" :style="{ width, height }"></div>
    </div>
</template>

<script>
import * as echarts from "echarts";
export default {
    props: {
        BarChartHorizontalId: {
            type: String,
            default: "BarChartHorizontalId",
        },
        xTitleColor: {
            type: String,
            default: "#91cc75",
        },

        width: {
            type: String,
            default: "100%",
        },
        height: {
            type: String,
            default: "200px",
        },
        echartsData: {
            type: Array,
            default: () => [0],
        },
        // x轴坐标title
        yAxisTitle: {
            type: Array,
            default: () => [],
        },
        legendData: {
            type: Array,
            default: () => [],
        },
        // title
        echartTitle: {
            type: Object,
            default: () => ({
                text: "",
                left: "left",
            }),
        },
    },
    data() {
        return {
            myChart: null,
            myOptions: {
                // legend: {
                //     data: this.legendData
                // },
                xAxis: {
                    type: "value",
                    axisLabel: {
                        color: "#fff",
                        fontSize: 10, //如果全部显示不开,可以选择字体大小
                        interval: 0, //控制是否全部显示
                        // rotate: 30//控制字体倾斜
                    },
                    splitLine: {
                        show: false,
                    },
                },
                yAxis: {
                    type: "category",
                    data: [],
                    axisLabel: {
                        color: "#fff",
                        fontSize: 6, //如果全部显示不开,可以选择字体大小
                        // interval:0 ,  //控制是否全部显示
                        // rotate: 20//控制字体倾斜
                    },
                },
                title: {
                    text: "",
                    left: "",
                },
                series: [
                    {
                        type: "bar",
                        data: [],
                        
                    },
                ],
            },
        };
    },
    mounted() {
        // 初始化图表
        this.initCharts();
    },
    updated() {
        // 初始化图表
        this.initCharts();
    },
    methods: {
        // 初始化图表
        initCharts() {
            this.myChart = echarts.init(
                document.getElementById(this.BarChartHorizontalId)
            );
            this.myChart.setOption(this.myOptions);
            this.myChart.setOption({
                yAxis: {
                    data: this.yAxisTitle,
                },
                title: {
                    text: this.echartTitle.text,
                    left: this.echartTitle.left,
                },
                series: [
                    {
                        data: this.echartsData,
                    },
                ],
            });
            window.addEventListener("resize", this.onResize);
        },
        onResize() {
            this.myChart.resize();
        },
    },
};
</script>

