<template>
    <div class="today-hot-container">
        <div class="public-box">
            <div class="tab">
                <div class="tab-btn">
                    <router-link :to="{ name: 'Home' }">今日预警</router-link>
                    <router-link :to="{ name: 'HotFollow' }">热点关注</router-link>
                </div>
                <div class="tab-body">
                    <!-- 表格 -->
                    <div class="table-box">
                        <div class="my-table-header">
                            <span style="width: 160px">名称</span>
                            <span style="width: 200px">时间</span>
                            <span style="width: 200px">内容摘要</span>
                            <span style="width: 110px">状态</span>
                        </div>
                        <div class="my-table-body">
                            <a
                                href="javascript:;"
                                @click="goToDetails(item)"
                                v-for="(item, index) in tableData"
                                :key="index"
                                class="my-table-content">
                                <div style="width: 160px; white-space: nowrap; overflow: hidden; text-overflow:ellipsis;">{{ item.address }}</div>
                                <div style="width: 200px; white-space: nowrap;">{{ item.occur_datetime }}</div>
                                <div style="width: 200px">{{ item.event_type_name }}</div>
                                <div style="width: 110px">
                                    <span class="text-red" v-if="item.status_name == '待反馈'">{{item.status_name}}</span>
                                    <span class="text-green" v-else-if="item.status == '已反馈'">{{item.status_name}}</span>
                                </div>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {http} from '@/utils/request'
export default {
    data() {
        return {
            tableData: [],
            total: 0,
            warningNumber: 0,
            handleNumber: 0,
        };
    },
    mounted() {
        this.requestTableData();
    },
    methods: {
        requestTableData() {
            let daiNum = 0;
            let yiNum = 0;
            let json = {username: "api", password: "a123456b"}
            http.get('https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/cmd/events', {params: {json}}).then(res => {
                this.tableData = res.data.result;
                this.total = this.tableData.length;
                this.tableData.forEach(item => {
                    console.log(item)
                    if(item.status_name === '待反馈') daiNum++;
                    if(item.status_name === '已反馈') yiNum++;
                    item.address = item.address.slice(6,item.address.length)
                    item.occur_datetime = item.occur_datetime.slice(5,16)
                })
                this.handleNumber = yiNum;
                this.warningNumber = daiNum;
            })
        },
        goToDetails(item) {
            this.$router.push({
                name: "EarlyWarning",
                query: {
                    details:{
                        item,
                        total:this.total,
                        warningNumber:this.warningNumber,
                        handleNumber:this.handleNumber,
                    },
                },
            });
        },
    },
};
</script>

<style lang="scss">
.today-hot-container {
    .public-box {
        padding: 27px 24px;
        .tab {
            .tab-btn {
                display: flex;
                a {
                    font-size: 40px;
                    color: #f6ffff;
                    margin-right: 39px;
                    line-height: 44px;
                    padding-bottom: 5px;
                    &.router-link-active {
                        color: #8dfec9;
                        border-bottom: 1px solid #8dfec9;
                    }
                }
            }
        }
        .table-box {
            .my-table-header{
                background: none;
                margin:20px 0 19px 0;
                font-size: 34px;
                color: #64BAF8;
            }
            .my-table-body {
                max-height: 410px;
                font-size: 26px;
                .my-table-content{
                    opacity: 0.7;
                    height: 60px;
                    line-height: 60px;
                }
                .my-table-content:nth-child(2n) {
                    background:none;
                }
                .my-table-content:nth-child(2n+1) {
                    background: linear-gradient(to right, rgba(74, 129, 169, 0) 0%, rgba(68, 124, 165, 0.6) 50%, rgba(74, 129, 169, 0) 100%,);
                }
            }
        }
    }
}
</style>